<template>
  <div>
    <van-nav-bar v-if="canBack == 1" title="我的订单" fixed style="height: 46px">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left"  @click="$toPage(-1)" />
    </van-nav-bar>
    <van-nav-bar v-if="canBack == 0" title="我的订单" fixed style="height: 46px">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left"  @click="goPersonal" />
    </van-nav-bar>
    <div style="height: 46px;"></div>
    
    <div class="tab_box" style="top: 46px;">
      <div :class="{'tab_title_activity':oIndex==0,'tab_title':true}" @click="switchOrderState(0)">全部</div>
      <div :class="{'tab_title_activity':oIndex==1,'tab_title':true}" @click="switchOrderState(1)">待付款</div>
      <div :class="{'tab_title_activity':oIndex==2,'tab_title':true}" @click="switchOrderState(2)">待发货</div>
      <div :class="{'tab_title_activity':oIndex==3,'tab_title':true}" @click="switchOrderState(3)">待收货</div>
      <div :class="{'tab_title_activity':oIndex==4,'tab_title':true}" @click="switchOrderState(4)">已签收</div>
      <div :class="{'tab_title_activity':oIndex==5,'tab_title':true}" @click="switchOrderState(5)">已完成</div>
      <div :class="{'tab_title_activity':oIndex==6,'tab_title':true}" @click="switchOrderState(6)">已取消</div>
      <div class="tab_line" :style="'transform: translateX('+oIndex*100+'%)'"></div>
    </div>
    <div class="placeholder_box"></div>

    <div class="search_order">
      <div class="search_order_title">下单时间：</div>
      <div class="start_time" @click="selectStartTime">{{start_time?start_time:"请选择开始时间"}}</div>
      <div style="flex: 1;text-align: center;">~</div>
      <div class="end_time" @click="selectEndTime">{{end_time?end_time:"请选择结束时间"}}</div>
      
    </div>
    <div class="search_order">
      <div class="search_order_title">订单号：</div>
      <input placeholder="输入订单号搜索" v-model="searchOrderNo" />
      <div class="search_order_Btn" @click="searchOrderFn">搜索</div>
    </div>
    
    
    <van-popup
      v-model="start_time_pop"
      position="bottom"
    >
      <van-datetime-picker
        v-model="start_time_format"
        type="date"
        :min-date="min_data_0"
        :max-date="max_data_0"
        @cancel="selectStartTimeHide"
        @confirm = "selectStartTimeAffirm"
      />
    </van-popup>
    
    <van-popup
      v-model="end_time_pop"
      position="bottom"
    >
      <van-datetime-picker
        v-model="end_time_format"
        type="date"
        :min-date="start_time_format"
        :max-date="max_data_0"
        @cancel="selectEndTimeHide"
        @confirm = "selectEndTimeAffirm"
      />
    </van-popup>

    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <div class="order_content_box" v-for="(item,index) in orderDataList" :key="index">
          <div class="order_num">
            <div class="order_num">订单编号{{item.orderno}}</div>
            <div class="order_status_txt">{{statusText(item)}}</div>
          </div>
          
          <div class="order_content">
            <div class="order_content_left">
              <div class="order_goods_img" @click="toDetails(item)">
                <img v-lazy="item.goods[0].imgURl" />
              </div>
              <div class="hidden-btn" v-if="item.payway == 4 || 
                item.ostatus == 4 || 
                item.ostatus == -4 || 
                item.ostatus == -3 ||
                ((item.ostatus == 3 || item.ostatus == 4) && (item.cstatus&256)==0)" >
                <van-icon name="ellipsis" size="20px" @click="showBtn(item)" />
                <div :class="{'hidden-btn-box':true,'hidden-btn-box_show':item._showBtn}" >
                  <!-- <div @click="getSOAPath(item)">药检报告下载</div> -->
                  <div @click="transfer(item)" v-if="item.payway == 4">转账说明</div>
                  <div @click="reOrder(item)" v-if="item.ostatus == 4">再次购买</div>
                  <div @click="deleteOrder(item)" v-if="item.ostatus == -4 || item.ostatus == 4 || item.ostatus == -3">删除</div>
                  <div @click="toSuppInvo(item)" v-if="(item.ostatus == 3 || item.ostatus == 4) && (item.cstatus&256)==0">补开发票</div>
                </div>
              </div>
            </div>
            
            <div class="order_content_right">
              <div class="order_goods_num">共<span>{{item.goods.length}}</span>种药品</div>
              <div class="order_sum">合计：<span>￥{{item.payamt}}</span></div>
              <div class="order_time">下单时间：{{item.odate}} {{item.otime}}</div>
              <div class="show_btn">
                <div class="show_btn_1" @click="afterApply(item)" v-if="(item.ostatus == 3 || item.ostatus == 4) && item.asstatus!==200">申请售后</div>
                <div class="show_btn_1" @click="isShowCancel(item)" v-if="canCancel(item)">取消订单</div>
                <!-- <div class="show_btn_0" @click="toEvaluate(item)" v-if="item.ostatus == 4 && (item.cstatus&128) == 0">评论</div> -->
                <div class="show_btn_0" @click="viewLogistics(item)" v-if="item.ostatus >= 2 && item.ostatus != -4">查看物流</div>
                <div class="show_btn_0" v-if="item.ostatus == 0 && item.payway == -1" @click="toPay(item)">立即付款</div>
                <div class="show_btn_0" v-if="item.ostatus == 3" @click="confirmReceipt(item)">确认签收</div>
              </div>
            </div>
          </div>
        </div>
        
      </van-list>
    </van-pull-refresh>
    
    <!-- 支付方式 -->
    <van-actionsheet
      class="payment"
      v-model="payment"
      :close-on-click-overlay="overlay"
    >
      <div
        v-for="(item, index) in payments"
        :key="index"
        @click="onSelectPay(index)"
      >
        <img :src="item.url" :alt="item.name" />
        <p class="name">{{ item.name }}</p>
      </div>
      <p class="cancel" @click="onCancel">稍后再付</p>
    </van-actionsheet>
  </div>
</template>

<script>
  import { Dialog } from 'vant' 
  export default {
    data() {
      return {
        searchOrderNo:'',//搜索订单号
        start_time:'',//选中的开始时间
        end_time:'',//选中的结束时间
        start_time_format:null,
        end_time_format:null,
        start_time_pop:false,//开始时间选择
        end_time_pop:false,//结束时间选择
        min_data_0:new Date('2019-01-01'),//选择开始的时间
        max_data_0:new Date(),//选择结束时间
        year:'',
        canBack:1,//控制返回
        oStatusArr:['','0','1','2','3','4','-4'],
        oIndex:0,//选项卡下标
        isLoading:false,//上拉刷新
        loading:false,//下拉加载
        pageIndex:1,//初始页码
        finished:false,//可以下拉加载
        orderDataList:[],//订单数据列表
        payment: false,
        payments: [
        { attr: 'wxpay', name: '微信支付', url: require("../../assets/pay/wx.png") },
        { attr: 'alipay', name: '支付宝支付', url: require("../../assets/pay/zfb.png") },
        { attr: 'xxpay', name: '线下支付', url: require("../../assets/pay/xx.png") }
      ],
        overlay: false,
        orderNo:null
      }
    },
    mounted(){
      if(this.$route.query.canBack == 0){
        this.canBack = 0;
      }
      this.oIndex = this.$route.query.index||0;
    },
    methods:{
      //药检报告下载
      getSOAPath(item){
        JNB.query({
          serverName: 'orderServer'+this.$ser_in(536862887||this.$loadUserInfo().comp.storeId),
          cls: 'OrderInfoModule',
          method: 'getSOAPath',
          params: {
            sku: 11015020003701 || item.goods[0].pdno,
            compid: 536862887||this.$loadUserInfo().comp.storeId,
            orderno: 1907240000147407 || item.orderno
          },
          callback :(data)=>{
            data = JSON.parse(data)
            if(data.code == 200){
              
            }else{
              this.$toast(data.message)
            }

          },
        })
      },
      selectStartTimeHide(){
        this.start_time_pop = false;
      },
      selectStartTimeAffirm(v){//选择开始时间
        let y = v.getFullYear();
        this.year = y;
        let m = v.getMonth() + 1;
        let d = v.getDate();
        this.start_time = `${y}-${m}-${d}`;
        this.start_time_format = new Date(this.start_time);
        this.end_time = '';
        this.end_time_format = null;
        this.start_time_pop = false;
      },
      selectStartTime(){
        this.start_time_pop = true;
      },
      selectEndTimeHide(){
        this.end_time_pop = false;
      },
      selectEndTimeAffirm(v){//选择结束时间
        let y = v.getFullYear();
        if(this.year != y){
          this.$toast('结束年份必须为'+this.year)
          return
        }
        let m = v.getMonth() + 1;
        let d = v.getDate();
        this.end_time = `${y}-${m}-${d}`;
        this.end_time_format = new Date(this.end_time);
        this.end_time_pop = false;
      },
      selectEndTime(){
        if(!this.start_time_format){
          this.$toast('请先选择开始时间')
          return
        }
        this.end_time_pop = true;
      },
      searchOrderFn(){
        if(this.year != '' && !this.end_time_format){
          this.$toast('请先选择结束时间')
          return
        }
        this.pageIndex = 1;
        this.finished = false;//解除下拉加载锁定
        this.loading = true;
        this.orderDataList = [];//清空原有商品数据
        this.getOrderListData();
      },
      switchOrderState(index){//切换订单状态
        this.end_time = '';
        this.start_time = '';
        this.end_time_format = null;
        this.start_time_format= null;
        this.searchOrderNo= '';
        this.year = '';
        this.oIndex = index;
        this.pageIndex = 1;
        this.finished = false;//解除下拉加载锁定
        this.loading = true;
        this.orderDataList = [];//清空原有商品数据
        this.getOrderListData();
      },
      onRefresh(){//上拉刷新
        this.pageIndex = 1;
        this.finished = false;//解除下拉加载锁定
        this.getOrderListData();
      },
      onLoad(){//下拉加载
        this.getOrderListData();
      },
      getOrderListData(){//获取订单列表
        let data = [this.year,this.oStatusArr[this.oIndex], this.searchOrderNo, '', this.start_time, this.end_time];
        JNB.query({
          serverName: 'orderServer'+this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls: 'OrderInfoModule',
          method: 'queryOrders',
          params: data,
          page:this.pageIndex,
          callback :(data)=>{
            data = JSON.parse(data);
            if(data.code == 200){
              this.isLoading = false;//如果是上拉刷新就停止刷新
              this.loading = false;//加载完成
              for(let i=0; i< data.data.length; i++) {
                if(!data.data[i].goods) {
                  data.data.splice(i, 1)
                  i--
                }
              }
              data.data.forEach(item => {
                //处理数据
                item._showBtn = false;
                this.$request_img({//获取商品图片
                  context: this,
                  arr: item.goods,
                  skuName:"pdno",
                  spuName:"spu"
                })
              });
              if(data.pageNo == 1){
                this.orderDataList = data.data;
              }else{
                this.orderDataList = this.orderDataList.concat(data.data);
              }
              if(data.total/data.pageSize<=this.pageIndex){
                this.finished = true;//锁定下拉加载
              }else{
                this.pageIndex = this.pageIndex+1;
              }
            }

          },
        })
      },
      statusText(val) {
        var text = "";
        switch (val.ostatus) {
          case 0:
            text = "等待付款";
            break;
          case 1:
            text = "等待发货";
            break;
          case 2:
            text = "等待收货";
            break;
          case 3:
            text = "已收货";
            break;
          case 4:
            text = "已完成";
            break;
          case -1:
            text = "申请售后";
            break;
          case -2:
            text = "申请售后中";
            break;
          case -3:
            text = "售后完成";
            break;
          case -4:
            text = "已取消";
            break;
        }
        if(val.asstatus == 200){
          text = "售后完成";
        }
        return text;
      },
      //是否可以取消订单
      canCancel(item) {
        let date = item.odate + " " + item.otime;
        date = date.substring(0,19);
        date = date.replace(/-/g,'/');
        var timestamp = new Date(date).getTime();
        var timestampNow = parseInt(new Date().getTime());    // 当前时间戳
        var times = timestampNow - timestamp;
        var thirtyMin = 30 * 60 * 1000;
        if (item.payway == 4 && item.ostatus ==0 && times < thirtyMin){
            return true
        }
        if (item.payway == 5 && item.ostatus == 1 && times < thirtyMin) {
            return true
        }
        if (item.ostatus == 0 && item.payway == -1) {
            return true
        }
      },
      showBtn(item){
        item._showBtn = !item._showBtn;
      },
      toDetails(item) {
        //订单详情
        this.$router.push({
          path: "/successfulTrade",
          query: {
            orderNo: item.orderno,
            back: 1
          }
        });
      },
      transfer(item) {
        item._showBtn = false;
        //转账说明
        this.$router.push({
          path: '/transferExplain'
        })
      },
      reOrder(item) {// 重新购买
        item._showBtn = false;
        // let arr = item.goods.map((value) => {
        //   return {
        //     compid: this.$loadUserInfo().comp.storeId,
        //     pdno: value.pdno,
        //     pnum: value.pnum,
        //     checked: 0
        //   }
        // })
        let arr = {
          orderno: item.orderno,
          compid: this.$loadUserInfo().comp.storeId
        }
        JNB.query({
          serverName: 'orderServer'+this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls: 'ShoppingCartModule',
          method: 'againShopCart',
          params: arr,
          callback :(data)=>{
            data = JSON.parse(data)
            if (data.code == 200) {
              this.$router.push({
                path: '/shoppingCart',
                query: {
                  againShopping: true
                }
              })
              //this.$toast('购物车添加成功')
            }
          },
        })
      },
      // 删除订单
      deleteOrder(item) {
        Dialog.confirm({
          title: '您确定要删除此订单?',
          message: '删除后，您将不能查询到该订单的信息！'
        }).then(() => {
          // on confirm
          let data = {
            orderno: item.orderno,
            cusno: item.cusno
          }
          JNB.query({
            serverName: 'orderServer'+this.$ser_in(item.cusno),
            cls: 'OrderOptModule',
            method: 'deleteOrder',
            params: data,
            callback :(data)=>{
              data = JSON.parse(data)
              if (data.code == 200) {
                this.$toast(data.message);
                this.pageIndex = 1;
                this.finished = false;//解除下拉加载锁定
                this.loading = true;
                this.orderDataList = [];//清空原有商品数据
                this.getOrderListData();
              }
            },
          })
        }).catch(() => {
          // on cancel
        });
      },
      toSuppInvo(item) {
        //补开发票
        this.$router.push({
          path: "/patchInvoice",
          query: {
            orderNo: item.orderno
          }
        });
      },
      afterApply(item) {
        //申请售后
        this.$router.push({
          path: "/applyAfter",
          query: {
            orderNo: item.orderno
          }
        });
      },
      isShowCancel(item) {
        //取消订单
        Dialog.confirm({
          title: '提示',
          message: '订单取消成功后将无法恢复，优惠券不再返还，支付优惠也将一并取消？'
        }).then(() => {          
          // on confirm
          let method = null;
          if(item.ostatus == 0 && item.payway == -1){
            method = "cancelOrder";
          }else{
            method = "cancelOffLineOrder";
          }
          let data = {
            orderno: item.orderno,
            cusno: item.cusno
          }
          JNB.query({
            serverName: 'orderServer'+this.$ser_in(item.cusno),
            cls: 'TranOrderOptModule',
            method: method,
            params: data,
            callback :(data)=>{
              data = JSON.parse(data)
              if (data.code == 200) {
                this.$toast('取消成功');
                this.pageIndex = 1;
                this.finished = false;//解除下拉加载锁定
                this.loading = true;
                this.orderDataList = [];//清空原有商品数据
                this.getOrderListData();
              }else{
                this.$toast(data.message);
              }
            },
          })
        }).catch(() => {
          // on cancel
        });
      },
      toEvaluate(item){
        //评论
        this.$router.push({
          path: "/evaluate",
          query: {
            orderNo: item.orderno
          }
        });
      },
      viewLogistics(item) {
        //查看物流
        this.$router.push({
          path: "/logistics",
          query: {
            orderNo: item.orderno
          }
        });
      },
      toPay(item) {
        //立即付款
        this.orderNo = item.orderno;
        this.payment = true;
      },
      onSelectPay(item) {
      if(item == 0 || item == 1 ) {
        JNB._requestNative(this.payments[item].attr,JSON.stringify({orderno: this.orderNo}),(data)=>{
          if (data == 0){
            if(this.$store.state.payResult){
              Dialog.alert({
                title: '温馨提示',
                message: '支付成功！'
              }).then(() => {
                this.onRefresh()
              });
            }
          }else{
            Dialog.alert({
              title: '支付失败',
              message: '请到订单详情页继续完成支付~'
            }).then(() => {
              this.$router.replace({
                path: "/successfulTrade",
                query: {
                  orderNo: this.orderNo,
                  back: 1
                }
              });
            });
          }
        })
      }else if(item == 2) {
        Dialog.confirm({
          title: '您确认付款方式为线下转账？',
          message: `一、请您在汇款时备注一块医药订单编号，这将会很大程度上缩短我们的核款时间并能尽快为您安排发货。<br>二、请于24小时内汇款并确保汇款金额与订单总金额一致，到账时间为1-3个工作日。<br>三、从下单之日起5天内如果还未付款并到账，系统将自动取消该订单。<br>四、我们将在收到款项后及时确认收款并安排发货，若款项汇出2天后未帮您收款确认，请及时拨打底部客服热线。`
        }).then(() => {
          JNB.query({
            serverName: "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
            cls: "PayModule",
            method: "offlinePay",
            params: {
              orderno: this.orderNo,
              compid: this.$loadUserInfo().comp.storeId,
              paytype: 4
            },
            callback: data => {
              let result = JSON.parse(data);
              if (result.code === 200) {
                this.$router.replace({
                  path: "/successfulTrade",
                  query: {
                    orderNo: this.orderNo,
                    back: 1
                  }
                });
              } else {
                this.$toast.fail(result.message);
              }
            }
          });

        }).catch(() => {
          // on cancel
        });
      }
      this.payment = false
    },
      onCancel() {
        this.payment = false
      },
      confirmReceipt(item) {
        //确认签收
        Dialog.confirm({
          title: '提示',
          message: '是否要确认签收?'
        }).then(() => {          
          // on confirm
          let data = {
            orderno: item.orderno,
            cusno: item.cusno
          }
          JNB.query({
            serverName: 'orderServer'+this.$ser_in(item.cusno),
            cls: 'OrderOptModule',
            method: 'confirmReceipt',
            params: data,
            callback :(data)=>{
              data = JSON.parse(data)
              if (data.code == 200) {
                this.$toast(data.message);
                this.pageIndex = 1;
                this.finished = false;//解除下拉加载锁定
                this.loading = true;
                this.orderDataList = [];//清空原有商品数据
                this.getOrderListData();
              }
            },
          })
        }).catch(() => {
          // on cancel
        });
      },
      goPersonal(){//返回个人中心
        this.$router.push({
          name: 'personal'
        })
      },
    },
  }
</script>

<style scoped>
  .start_time,.end_time{
    width: 240px;
    padding: 0 20px;
    height: 60px;
    background-color: #FFFFFF;
    color: #666666;
    line-height: 60px;
    border: 1px solid #EEEEEE;
  }
  .search_order{
    display: flex;
    height: 80px;
    font-size: 24px;
    padding:0 20px;
    line-height: 80px;
    align-items: center;
  }
  .search_order_title{
    width: 120px;
  }
  .search_order input{
    display: block;
    height: 60px;
    line-height: 60px;
    border: none;
    padding: 0 20px;
    border: 1px solid #EEEEEE;
    flex: 1;
    margin-right: 20px;
  }
  .search_order_Btn{
    width: 120px;
    height: 50px;
    background-color: #FF0000;
    color: #FFFFFF;
    border-radius: 6px;
    text-align: center;
    line-height: 50px;
  }
  .tab_box{
    z-index: 5;
    height: 90px;
    display: flex;
    line-height: 90px;
    position: fixed;
    left: 0;
    right: 0;
    border-bottom: 1px solid #EEEEEE;
    background-color: #FFFFFF;
    color: #7d7e80;
    overflow: scroll;
  }
  .tab_title{
    flex: 20% 0 0;
    text-align: center;
    font-size: 28px;
    height: 90px;
    line-height: 90px;
  }
  .tab_title_activity{
    color: #ED2F26;
  }
  .tab_line{
    position: absolute;
    bottom: 0;
    width: 20%;
    height: 4px;
    background-color: #ED2F26;
